export default {
  name: 'FloatingSaveButton',
  props: {
    loading: {
      type: Boolean,
      default: false
    },
    show: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      visible: false
    };
  },
  watch: {
    show: {
      immediate: true,
      handler(val) {
        if (val) {
          //  Element Plus 
          setTimeout(() => {
            this.visible = true;
          }, 600);
        } else {
          this.visible = false;
        }
      }
    }
  },
  methods: {
    handleClick() {
      if (!this.loading) {
        this.$emit('click');
      }
    }
  }
};